import { keysAsString, difference } from './utils'

/**
 * Warns when the same language is added to the state
 * @param  {Array<Object>} languages
 * @param  {Object} language
 * @return {Boolean}
 */
export const defineUniqueLanguage = (languages, { code }) => {
  return languages.find(language => {
    const exists = language.code === code

    if (exists) {
      // eslint-disable-next-line
      console.error(`"${code}" already exists in the list of languages`)
    }

    return exists
  })
}

/**
 * Warns when required fields are not created languages object.
 * @param  {Object} language
 * @return {Boolean}
 */
export const defineLanguage = (language) => {
  const mandatory = ['code', 'key', 'urlPrefix']
  const languageKeys = Object.keys(language)
  const differences = difference(mandatory, languageKeys)

  if (differences.length) {
    // eslint-disable-next-line
    console.error(
      '[vue-i18n-manager] ' +
      `Invalid language definition. Property ${keysAsString(differences)} missing.`
    )
  }

  return !differences.length
}

/**
 * Warns if the default language code matches at least one of the provided languages,
 * otherwise the application could break.
 * @param  {Array} languages
 * @param  {String} code
 * @return {Boolean}
 */
export const defineLanguages = (languages, code) => {
  const defaultMatch = languages.filter(language => {
    defineLanguage(language)
    return language.code === code
  })

  if (!defaultMatch) {
    // eslint-disable-next-line
    console.error(
      '[vue-i18n-manager] ' +
      'The default code must matches at least one language in the provided list'
    )
  }

  return defaultMatch
}

/**
 * Checks invalid and deprecated keys
 * @param  {Array} allowedKeys
 * @param  {Array} paramsKeys
 * @param  {String} context
 * @param  {Array}  [deprecatedKeys=[]]
 * @return {Boolean}
 */
export const defineKeys = (newKeys, allowedKeys, context, deprecatedKeys = []) => {
  const invalidKeyes = difference(newKeys, allowedKeys)

  if (invalidKeyes.length) {
    const plural = invalidKeyes.length > 1

    // eslint-disable-next-line
    console.error(
      '[vue-i18n-manager] ' +
      `Propert${plural ? 'ies' : 'y'} ${keysAsString(invalidKeyes)} not valid for ${context}`
    )
  }

  return true
}

export function defineOptionsKeys (options) {
  return defineKeys(
    Object.keys(options),
    ['store', 'router', 'config', 'proxy'],
    'plugin options'
  )
}



